@font-face {
  font-family: "Raleway";
  src: url("fonts/Raleway-Medium.woff2") format("woff2"), url("fonts/Raleway-Medium.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Raleway";
  src: url("fonts/Raleway-SemiBold.woff2") format("woff2"), url("fonts/Raleway-SemiBold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
h1,
h2,
h3,
h4,
h5,
h6,
label,
.ci-text,
.copyright .copyright-text,
.copyright .copyright-link li a,
.sitemap .sitemap-link li a,
.sitemap p,
.header-right a,
.header-left .site-navifation li a {
  font-family: "Raleway";
}

.wrapper {
  position: relative;
}

body.lock {
  overflow-y: hidden;
}

.page-content {
  max-width: 962px;
  margin: auto;
}
.page-content h1 {
  font-size: 24px;
  margin: 32px 0 16px;
  font-weight: bold;
}
@media screen and (min-width: 768px) {
  .page-content h1 {
    margin: 24px 0 20px;
  }
}
@media screen and (min-width: 992px) {
  .page-content h1 {
    font-size: 32px;
