.slider-block,
.mobile-slider-block {
  overflow: hidden;
}
.slider-block img,
.mobile-slider-block img {
  max-width: 100%;
  height: auto;
  display: block;
}

.slider-block {
  display: none;
}
@media screen and (min-width: 768px) {
  .slider-block {
    display: block;
  }
}
@media xxl {
  .slider-block {
    max-width: 1400px;
    margin: auto;
  }
}

.mobile-slider-block {
  display: block;
}
.mobile-slider-block.slick-dotted.slick-slider {
