.mobile-menu-block {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
}
@media screen and (min-width: 768px) {
  .mobile-menu-block {
    display: none !important;
  }
}
.mobile-menu-block.active {
  display: block;
}
.mobile-menu-block .mobile-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1;
}
.mobile-menu-block .mobile-navigation {
  position: absolute;
  top: 0;
  right: 0;
  width: 300px;
  height: 100%;
  background-color: #ffffff;
  z-index: 2;
  margin: 0;
  padding: 10px 0 0;
  list-style: none;
}
.mobile-menu-block .mobile-navigation li {
