  display: block;
  color: #000000;
  text-decoration: none;
}
.mobile-menu-block .mobile-close-menu {
  position: absolute;
  top: 10px;
  right: 20px;
  font-size: 20px;
  z-index: 4;
  padding: 4px 12px;
  border-radius: 4px;
  background-color: #dfdfdf;
  display: block;
  text-decoration: none;
  color: #000000;
}

