  margin: 24px auto;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.menu-type .menu-list .menu-item {
  flex: 0 0 100%;
  margin: 0 0 30px;
}
@media screen and (min-width: 768px) {
  .menu-type .menu-list .menu-item {
    flex: 0 0 calc((100% - 15px) / 2);
    margin: 0 15px 30px 0;
  }
  .menu-type .menu-list .menu-item:nth-child(2n) {
    margin-right: 0;
  }
}
@media screen and (min-width: 992px) {
  .menu-type .menu-list .menu-item {
    flex: 0 0 calc((100% - 30px) / 3);
    margin: 0 15px 30px 0;
  }
  .menu-type .menu-list .menu-item:nth-child(2n) {
    margin-right: 15px;
  }
  .menu-type .menu-list .menu-item:nth-child(3n) {
    margin-right: 0;
  }
}
@media screen and (min-width: 1200px) {
  .menu-type .menu-list .menu-item {
    flex: 0 0 calc((100% - 45px) / 4);
  }
  .menu-type .menu-list .menu-item:nth-child(3n) {
    margin-right: 15px;
  }
  .menu-type .menu-list .menu-item:nth-child(4n) {
    margin-right: 0;
  }
}
.menu-type .menu-slider {
  margin: 24px auto;
}
.menu-type .menu-slider .slick-list {
  margin: 0 -15px 0 0;
}
.menu-type .menu-slider .slick-slide {
  margin: 0 15px 0 0;
}
.menu-type .menu-slider .menu-prev,
.menu-type .menu-slider .menu-next {
  border-radius: 50em;
  position: absolute;
  top: 50%;
  width: 40px;
  height: 40px;
  content: "";
  z-index: 2;
  margin-top: -80px;
}
.menu-type .menu-slider .menu-prev {
  left: 20px;
  background: transparent url("img/nav-left.png") no-repeat center center;
  background-size: cover;
}
.menu-type .menu-slider .menu-next {
  right: 20px;
  background: transparent url("img/nav-right.png") no-repeat center center;
  background-size: cover;
}
.menu-type .menu-item {
  flex: 0 0 calc((100% - 30px) / 3);
  margin: 0 15px 0 0;
}
.menu-type .menu-item:last-child {
  margin-right: 0;
}
.menu-type .menu-item .menu-image {
  max-width: 100%;
  height: auto;
  display: block;
  border-radius: 8px;
  margin: 0 0 8px;
}
.menu-type .menu-item .menu-name {
  font-weight: bold;
  margin: 0 0 4px;
  font-size: 16px;
}
.menu-type .menu-item .menu-description {
  font-size: 14px;
  line-height: 1.5;
  margin: 0 0 12px;
}
@media screen and (min-width: 992px) {
  .menu-type .menu-item .menu-description {
    font-size: 12px;
  }
}
@media screen and (min-width: 1200px) {
  .menu-type .menu-item .menu-description {
    font-size: 14px;
  }
}
.menu-type .menu-item .menu-action .menu-stock {
  font-weight: bold;
}
.menu-type .menu-item .menu-action .menu-stock.in {
  color: #00704a;
}
.menu-type .menu-item .menu-action .menu-stock.out {
  color: #e3ae0a;
}
.menu-type .menu-item .menu-action .menu-price {
  text-align: right;
}

